/* make variables */
:root {
  --lightgray: #D9D9D9;
  --gray: #9c9c9c;
  --orange: #f48915;
  --darkGrey: #464D53;
  --caloryCard: #656565;
  --planCard: linear-gradient(210.41deg, #D9F503 1.14%, #ebff50 100.75%);
  --appColor:  #0e0e0e;
  --limeGreen: #D9F503;
}

@font-face {
  font-family: 'ElectricShocker';
  src: url('./assets/font/ElectricShocker-2y8d.ttf') format('truetype');
}

@font-face {
  font-family: 'Montserrat';
  src: url('./assets/font/Montserrat-Medium.otf') format('opentype');
}

@font-face {
  font-family: 'MontserratThin';
  src: url('./assets/font/Montserrat-Thin.otf') format('opentype');
}

@font-face {
  font-family: 'MontserratLight';
  src: url('./assets/font/Montserrat-Light.otf') format('opentype');
}

.App {
  /* background-image: url(data:image/png;base64,); */
  mix-blend-mode: overlay;
  background-color: var(--appColor);
  display: flex;
  flex-direction: column;
  gap: 6rem;
  overflow: hidden;
  scroll-behavior: smooth;
}
::-webkit-scrollbar{
  display: none;
}

.stroke-text {
  color: transparent;
  font-family: 'ElectricShocker', sans-serif;
  -webkit-text-stroke-width: 1px;
  -webkit-text-stroke-color: #fff;
}

.stroke-text2 {
  color: transparent;
  font-family: 'Montserrat', sans-serif;
  -webkit-text-stroke-width: 1px;
  -webkit-text-stroke-color: #fff;
}

.blur {
  background: rgba(217, 245, 3, 0.69);
  position: absolute;
  border-radius: 50%;
  filter: blur(150px);
  z-index: -20;
}